.philosophy-step {
  background-image: linear-gradient(185deg, #ffffff 0%, #f9f9f9 50%);
  background-position: 0% 0%;
  background-repeat: repeat;
  background-size: cover;
}

@keyframes typewriter {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}

.pricing-card {
  height: 790px;

  &.recommended {
    height: 840px;
  }
}
